<template>
  <div class="course-create-or-edit">
    <!-- 页头（返回） -->
    <el-page-header
      :content="isEdit ? '编辑课程' : '添加课程'"
      @back="goBack"></el-page-header>
    <!-- 课程信息表单 -->
    <el-card>
      <!-- 步骤条 -->
      <div slot="header">
        <el-steps
          :active="activeStep"
          simple>
          <el-step
            v-for="(step, idx) in steps"
            :key="step.id"
            :title="step.title"
            :icon="step.icon"
            @click.native="activeStep = idx"></el-step>
        </el-steps>
      </div>
      <el-form
        label-width="80px"
        :model="course"
        :rules="courseRules">
        <!-- 基本信息 -->
        <div v-show="activeStep === 0">
          <el-form-item
            label="课程名称"
            prop="courseName">
            <el-input
              v-model="course.courseName"
              maxlength="50">
              <template slot="append">{{ course.courseName.length }}/50</template>
            </el-input>
          </el-form-item>
          <el-form-item
            label="课程简介"
            prop="brief">
            <el-input
              v-model="course.brief"
              maxlength="100" >
              <template slot="append">{{ course.brief.length }}/100</template>
            </el-input>
          </el-form-item>
          <el-form-item
            label="课程概述"
            prop="previewFirstField">
            <el-input
              placeholder="概述1"
              v-model="course.previewFirstField"
              maxlength="20"
              style="width:49%; min-width: 300px; margin-right: 15px;">
              <template slot="append">{{ course.previewFirstField.length }}/20</template>
            </el-input>
            <el-input
              placeholder="概述2"
              v-model="course.previewSecondField"
              maxlength="20"
              style="width:49%; min-width: 300px;">
              <template slot="append">{{ course.previewSecondField.length }}/20</template>
            </el-input>
          </el-form-item>
          <el-form-item
            label="讲师姓名"
            prop="teacherDTO.teacherName">
            <el-input
              v-model="course.teacherDTO.teacherName"
              maxlength="50" >
              <template slot="append">{{ course.teacherDTO.teacherName.length }}/50</template>
            </el-input>
          </el-form-item>
          <el-form-item
            label="讲师简介"
            prop="teacherDTO.description">
            <el-input
              v-model="course.teacherDTO.description"
              maxlength="100" >
              <template slot="append">{{ course.teacherDTO.description.length }}/100</template>
            </el-input>
          </el-form-item>
          <el-form-item
            label="课程排序"
            prop="sortNum">
            <el-input-number
              label="描述文字"
              controls-position="right"
              v-model="course.sortNum"></el-input-number>
          </el-form-item>
        </div>
        <!-- 课程封面 -->
        <div v-show="activeStep === 1">
          <course-image
            v-model="course.courseListImg"
            label="课程封面"
            :fileSizeLimit="150"
            imgReq="建议尺寸：230*300px，JPG、PNG格式，图片小于150K"></course-image>
          <course-image
            v-model="course.courseImgUrl"
            label="解锁封面"
            :fileSizeLimit="150"
            imgReq="建议尺寸：750*422px，JPG、PNG格式，图片小于150K"></course-image>
        </div>
        <!-- 销售信息 -->
        <div v-show="activeStep === 2">
          <el-form-item
            label="售卖价格"
            prop="price">
            <el-input
              v-model="course.discounts"
              type="number"
              :min="0" >
              <template slot="append">元</template>
            </el-input>
          </el-form-item>
          <el-form-item
            label="商品原价">
            <el-input
              v-model="course.price"
              type="number"
              :min="0" >
              <template slot="append">元</template>
            </el-input>
          </el-form-item>
          <el-form-item label="销量">
            <el-input
              v-model="course.sales"
              type="number"
              :min="0" >
              <template slot="append">单</template>
            </el-input>
          </el-form-item>
          <el-form-item
            label="活动标签">
            <el-input
              v-model="course.discountsTag">
            </el-input>
          </el-form-item>
        </div>
        <!-- 秒杀信息 -->
        <div v-show="activeStep === 3">
          <el-form-item label="秒杀开关">
            <el-switch
              v-model="course.activityCourse"
              active-color="#13ce96"
              inactive-color="#ff4949"></el-switch>
          </el-form-item>
          <template v-if="course.activityCourse">
            <el-form-item
              label="开始时间">
              <el-date-picker
                v-model="course.activityCourseDTO.beginTime"
                type="datetime"
                placeholder="选择日期时间"></el-date-picker>
            </el-form-item>
            <el-form-item
              label="结束时间">
              <el-date-picker
                v-model="course.activityCourseDTO.endTime"
                type="datetime"
                placeholder="选择日期时间"></el-date-picker>
            </el-form-item>
            <el-form-item
              label="秒杀价">
              <el-input
                v-model="course.activityCourseDTO.amount"
                type="number"
                :min="0" >
                <template slot="append">元</template>
              </el-input>
            </el-form-item>
            <el-form-item
              label="库存">
              <el-input
                v-model="course.activityCourseDTO.stock"
                type="number"
                :min="0" >
                <template slot="append">个</template>
              </el-input>
            </el-form-item>
          </template>
        </div>
        <!-- 课程详情 -->
        <div v-show="activeStep === 4">
          <el-form-item
            label="课程详情">
            <text-editor
              v-model="course.courseDescriptionMarkDown"></text-editor>
          </el-form-item>
          <el-form-item
            label="是否上架">
            <el-switch
              v-model="course.status"
              active-value="1"
              inactive-value="0"
              active-color="#13ce96"
              inactive-color="#ff4949" ></el-switch>
          </el-form-item>
          <el-form-item>
            <el-button
              type="primary"
              @click="handleSave">保存</el-button>
          </el-form-item>
        </div>
        <el-form-item>
          <el-button
            v-if="activeStep !== steps.length - 1"
            @click="activeStep++">下一步</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import {
  getCourseById,
  saveOrUpdateCourse
} from '@/services/course'
import CourseImage from './course-image.vue'
import TextEditor from '@/components/text-editor'

export default {
  name: 'CourseCreateOrEdit',
  props: {
    courseId: [Number, String],
    isEdit: {
      type: Boolean
    }
  },
  components: {
    CourseImage,
    TextEditor
  },
  data () {
    return {
      activeStep: 0,
      steps: [
        { id: 1, title: '基本信息', icon: 'el-icon-edit' },
        { id: 2, title: '课程封面', icon: 'el-icon-upload' },
        { id: 3, title: '销售信息', icon: 'el-icon-picture' },
        { id: 4, title: '秒杀信息', icon: 'el-icon-picture' },
        { id: 5, title: '课程详情', icon: 'el-icon-picture' }
      ],
      course: {
        courseName: '',
        brief: '',
        teacherDTO: {
          teacherName: '',
          teacherHeadPicUrl: '',
          position: '',
          description: ''
        },
        // 内容必须为HTML内容
        courseDescriptionMarkDown: '<p></p>',
        price: 0,
        discounts: 0,
        priceTag: '',
        discountsTag: '',
        isNew: true,
        isNewDes: '',
        courseListImg: '',
        courseImgUrl: '',
        sortNum: 0,
        previewFirstField: '',
        previewSecondField: '',
        status: 0,
        sales: 0,
        activityCourse: false,
        activityCourseDTO: {},
        autoOnlineTime: ''
      },
      courseRules: {
        courseName: [
          { required: true, message: '请输入课程名称', trigger: 'blur' }
        ],
        brief: [
          { required: true, message: '请输入课程简介', trigger: 'blur' }
        ],
        previewFirstField: [
          { required: true, message: '请输入课程概述', trigger: 'blur' }
        ],
        teacherDTO: {
          teacherName: [
            { required: true, message: '请输入讲师姓名', trigger: 'blur' }
          ],
          description: [
            { required: true, message: '请输入讲师简介', trigger: 'blur' }
          ]
        },
        sortNum: [
          { required: true, message: '请输入课程排序', trigger: 'blur' }
        ],
        price: [
          { required: true, message: '请输入课程售价', trigger: 'blur' }
        ]
      }
    }
  },
  created () {
    if (this.isEdit) {
      this.loadCourseInfo()
    }
  },
  methods: {
    async loadCourseInfo () {
      const { data } = await getCourseById(this.courseId)
      if (data.code === '000000') {
        if (!data.data.activityCourse) {
          data.data.activityCourseDTO = {
            beginTime: '',
            endTime: '',
            amount: 0,
            stock: 0
          }
        }
        this.course = data.data
      }
    },
    async handleSave () {
      const { data } = await saveOrUpdateCourse(this.course)
      if (data.code === '000000') {
        this.$message.success('提交成功')
        this.$router.push({
          name: 'course'
        })
      }
    },
    goBack () {
      this.$router.push({
        name: 'course'
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.course-create-or-edit {
  .el-step {
    cursor: pointer;
  }
  .el-page-header {
    margin-bottom: 20px;
  }
}
</style>
