<template>
  <div class="course-image">
    <el-form-item
      :label="label">
      <el-progress
        v-if="isUploading"
        type="circle"
        :percentage="uploadPercentage"
        :status="uploadPercentage === 100 ? 'success' : undefined"
        :width="178"
      ></el-progress>
      <el-upload
        v-else
        class="avatar-uploader"
        action=""
        :show-file-list="false"
        :before-upload="beforeAvatarUpload"
        :http-request="handleUpload">
        <img
          v-if="value"
          :src="value"
          class="avatar" />
        <i
          v-else
          class="el-icon-plus avatar-uploader-icon"></i>
      </el-upload>
      <el-tag
        v-if="imgReq"
        type="info">{{ imgReq }}</el-tag>
    </el-form-item>
  </div>
</template>

<script>
import {
  uploadCourseImage
} from '@/services/course'

export default {
  name: 'CourseImage',
  props: {
    value: {
      type: String
    },
    label: {
      type: String
    },
    fileSizeLimit: {
      type: Number
    },
    imgReq: {
      type: String
    }
  },
  data () {
    return {
      isUploading: false,
      uploadPercentage: 0
    }
  },
  methods: {
    beforeAvatarUpload (file) {
      const isJPG = file.type === 'image/jpeg'
      const isPNG = file.type === 'image/png'
      const isLtxK = (file.size / 1024) < this.fileSizeLimit

      if (!isJPG && !isPNG) {
        this.$message.error('上传头像图片只能是 JPG 或 PNG 格式')
      }
      if (!isLtxK) {
        this.$message.error(`上传头像图片大小不能超过 ${this.fileSizeLimit} KB！`)
      }
      return (isJPG || isPNG) && isLtxK
    },
    async handleUpload (option) {
      this.isUploading = true
      const fd = new FormData()
      fd.append('file', option.file)
      const { data } = await uploadCourseImage(fd, event => {
        this.uploadPercentage = Math.floor(event.loaded / event.total * 100)
      })
      if (data.code === '000000') {
        this.$emit('input', data.data.name)
      }
      this.isUploading = false
      this.uploadPercentage = 0
    }
  }
}
</script>

